table {
    margin-right: auto;
    margin-left: auto;
}

th {
    width: 45%;
}

.rating {
    display: inline;
    color: var(--secondary-text-color);
}

.positive {
    color: green;
}

.negative {
    color: red;
}

.wrong-quote-a {
    color: var(--text-color) !important;
}

.info-page-0 {
    display: flex;
    flex-direction: column;
    place-content: center;
    gap: 2rem;
}

@media only screen and (min-width: 768px) {
    .info-page-0 {
        flex-direction: row;
    }
}

.info-page-data {
    display: flex;
    flex-direction: column;
    place-content: center;
    text-align: center;
}

.info-page-q-col {
    display: flex;
    flex-direction: column;
    place-content: center;
}

.info-page-q-col  span {
    display: flex;
    flex-direction: row;
    place-content: center;
    gap:  1ch;
}

#disclaimer {
    display: flex;
    flex-direction: row;
    margin-top: 2rem;
}

.button-container-a {
    --img-size: 1rem;
    display: inline-block;
    width: var(--img-size);
    height: var(--img-size);
}

#report {
    --img-size: 3.2rem;
}

.button-container-a img {
    display: block;
    width: var(--img-size);
    height: var(--img-size);
}

/* author info page */
#author-info {
    padding: 0.3125rem;
    border-right: 0.3125rem;
    background-color: var(--secondary-bg);
}
